.home-tools-container {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: flex-start;
  padding: 1rem;
}

.home-tools-grid {
  display: grid;
  grid-template-columns: 33% 33% 33%;
  margin-top: 1.5rem;
}

.home-tools-grid-item {
  margin: 0.375rem;
}