/* MARK: Global Variables */

:root {
  --header-height: 3rem;

  /* Colors */
  --hue-val: 150;
  --hue-error-val: 0;

  --color-primary           : hsl(var(--hue-val), 69%, 50%);
  --color-primary-alt       : hsl(var(--hue-val), 57%, 45%);
  --color-primary-light     : hsl(var(--hue-val), 92%, 85%);
  --color-title             : hsl(var(--hue-val), 0%, 15%);
  --color-text              : hsl(var(--hue-val), 0%, 35%);
  --color-text-light        : hsl(var(--hue-val), 0%, 55%);
  --color-text-super-light  : hsl(var(--hue-val), 0%, 75%);
  --color-text-colored      : hsl(var(--hue-val), 55%, 40%);
  --color-background        : hsl(var(--hue-val), 0%, 100%);
  --color-background-alt    : hsl(var(--hue-val), 0%, 95%);
  --color-border            : hsl(var(--hue-val), 0%, 75%);
  --color-shadow            : hsl(var(--hue-val), 0%, 85%);
  --color-error             : hsl(var(--hue-error-val), 69%, 50%);

  /* Fonts */
  --font-name: 'Poppins';

  --font-size-h1      : 2.0rem;
  --font-size-h2      : 1.5rem;
  --font-size-h3      : 1.25rem;
  --font-size-h4      : 1.1rem;
  --font-size-body    : .938rem;
  --font-size-small   : .813rem;

  --font-weight-medium: 500;
  --font-weight-semibold: 600;
}

/* MARK: Base */
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  user-select: none;
}

html {
  scroll-behavior: smooth;
}

div {
  white-space: pre-wrap
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

input, select, textarea {
  font-family: inherit;
  text-decoration: none;
}

body {
  margin            : 0 0 var(--header-height) 0;
  font-family       : var(--font-name);
  font-size         : var(--font-size-body);
  background-color  : var(--color-background);
  color             : var(--color-text);
  line-height       : 1.4;
}

h1 {
  font-size: var(--font-size-h1);
  color: var(--color-title);
  font-weight: var(--font-weight-semibold);
  line-height: 1.3;
}

h2 {
  font-size: var(--font-size-h2);
  color: var(--color-title);
  font-weight: var(--font-weight-semibold);
  line-height: 1.3;
}

h3 {
  font-size: var(--font-size-h3);
  color: var(--color-title);
  font-weight: var(--font-weight-semibold);
  line-height: 1.3;
}

p {
  font-size: var(--font-size-body);
  color: var(--color-text);
}

.app {
  margin-left: auto;
  margin-right: auto;
  max-width: 700px;
}

/* MARK: Margin */
.m-200  { margin: 2.0rem; }
.m-150  { margin: 1.5rem; }
.m-100  { margin: 1.0rem; }
.m-50   { margin: 0.5rem; }
.m-37   { margin: 0.375rem; }
.m-25   { margin: 0.25rem; }
.mt-200 { margin-top: 2.0rem; }
.mt-150 { margin-top: 1.5rem; }
.mt-100 { margin-top: 1.0rem; }
.mt-50  { margin-top: 0.5rem; }
.mt-37  { margin-top: 0.375rem; }
.mt-25  { margin-top: 0.25rem; }
.ml-200 { margin-left: 2.0rem; }
.ml-150 { margin-left: 1.5rem; }
.ml-100 { margin-left: 1.0rem; }
.ml-50  { margin-left: 0.5rem; }
.ml-37  { margin-left: 0.375rem; }
.ml-25  { margin-left: 0.25rem; }
.mr-200 { margin-right: 2.0rem; }
.mr-150 { margin-right: 1.5rem; }
.mr-100 { margin-right: 1.0rem; }
.mr-50  { margin-right: 0.5rem; }
.mr-37  { margin-right: 0.375rem; }
.mr-25  { margin-right: 0.25rem; }
.mb-200 { margin-bottom: 2.0rem; }
.mb-150 { margin-bottom: 1.5rem; }
.mb-100 { margin-bottom: 1.0rem; }
.mb-50  { margin-bottom: 0.5rem; }
.mb-37  { margin-bottom: 0.375rem; }
.mb-25  { margin-bottom: 0.25rem; }

/* MARK: Padding */
.p-200 { padding: 2.0rem; }
.p-150 { padding: 1.5rem; }
.p-100 { padding: 1.0rem; }
.pt-200 { padding-top: 2.0rem; }
.pt-150 { padding-top: 1.5rem; }
.pt-100 { padding-top: 1.0rem; }
.pl-200 { padding-left: 2.0rem; }
.pl-150 { padding-left: 1.5rem; }
.pl-100 { padding-left: 1.0rem; }
.pr-200 { padding-right: 2.0rem; }
.pr-150 { padding-right: 1.5rem; }
.pr-100 { padding-right: 1.0rem; }
.pb-200 { padding-bottom: 2.0rem; }
.pb-150 { padding-bottom: 1.5rem; }
.pb-100 { padding-bottom: 1.0rem; }

.font-size-body {
  font-size: var(--font-size-normal);
}

.font-size-small {
  font-size: var(--font-size-small);
}

.font-size-tiny {
  font-size: var(--font-size-tiny);
}

.font-weight-semibold {
  font-weight: var(--font-weight-semibold);
}

.font-weight-medium {
  font-weight: var(--font-weight-medium);
}

.color-primary {
  color: var(--color-primary);
}

.color-text {
  color: var(--color-text);
}

.color-text-light {
  color: var(--color-text-light);
}

.color-text-colored {
  color: var(--color-text-colored)
}

.color-error {
  color: var(--color-error);
}

.text-align-left {
  text-align: left;
}

.text-align-center {
  text-align: center;
}

.img-masking-blobs {
  border-radius: 35% 35% 35% 35% / 35% 35% 35% 35%;
  box-shadow: 0.5rem 0.5rem 0.5rem var(--color-shadow);
}

.user-interaction-disabled {
  pointer-events: none;
}